import React, { PureComponent } from 'react';
import { Formik, Form, ErrorMessage } from 'formik';
import { string, object } from 'yup';
import { compose } from 'redux';
import get from 'lodash/get';

import FullpageVerticalCenter from 'components/FullpageVerticalCenter';
import Box from 'components/Box';
import Text from 'components/Text';
import Modal from 'components/Modal';
import Link from 'components/Link';
import Input from 'components/Input';
import Button from 'components/Button';
import Dropdown from 'components/Dropdown';
import Title from 'components/Title';
import Progress from 'components/Progress';
import Container from 'components/Container';
import { mobileOrDesktop } from 'components/ThemeProvider/theme'

import BodyPoints from './BodyPoints';
import StepNumber from './StepNumber';
import creatingHoc from './creatingHoc'
import Layout from '../Layout';
import withResponsive from '../../hoc/withResponsive'
import littlebody from './littlebody.svg'

export const options = [{label: '1-2天'}, {label: '3-7天'}, {label: '7天以上'}]

const schema = object().shape({
  pain: string().required('請輸入症狀'),
  duration: string().required('請選擇持續天數')
})

class Step1 extends PureComponent {
  state = {

  }

  handleNewDot = (pos) => {
    const { newDot, dots } = this.props
    newDot(pos)
    this.setState({
      openDot: dots.length,
    })
  }

  handleDotClick = index => {
    this.setState({ openDot: index })
  }

  handleClose = () => {
    this.setState({ openDot: false })
  }

  handleRemove = () => {
    const { removeDot } = this.props
    removeDot(this.state.openDot)
    this.setState({ openDot: false })
  }

  render() {
    const { dots, setDotData, isMobile } = this.props
    const { openDot } = this.state
    // console.log(dots[openDot], openDot)
    return (
      <Layout bg="lightBgOrange" overflowX="hidden">
        <StepNumber>1</StepNumber>
        <Container py="2.5em" position="relative">
          <Progress step={1} />
          <Box mt="1em">
            <Title title="點擊人偶圖來標示不適處" description="看診前先詳細紀錄不舒服的狀況，有助於在診間中與醫生討論病情" icon={littlebody} />
            <Box
              my={mobileOrDesktop(0, '0.5em')}
              mx={mobileOrDesktop('10%', '30%')}
              bg="bodyBgOrange"
              px="5%"
              pt="2.5em"
              borderRadius="1em"
              boxShadow="-2px 2px rgba(0,0,0,0.1)"
            >
              <BodyPoints
                onNewDot={this.handleNewDot}
                onDotClick={this.handleDotClick}
                dots={dots}
              />
              <Text py="1em" textAlign="center">再次點擊不適部位即可更改</Text>
            </Box>
            <Modal
              isOpen={typeof openDot === 'number'}
              customStyles={{ width: isMobile ? 'auto' : '25%'}}
            >
              <Formik
                initialValues={{
                  duration: get(dots[openDot], 'duration'),
                  pain: get(dots[openDot], 'pain'),
                }}
                onSubmit={(values) => {
                  setDotData({
                    index: openDot,
                    ...values
                  })
                  this.handleClose()
                }}
                validationSchema={schema}
              >
                {({ handleChange, values }) => (
                  <Form>
                    <Container>
                      <Text fontSize="1.5em" pt="1em" color="titleBlue">症狀描述</Text>
                      <Text>依自身狀況填入不適程度與頻率</Text>
                      <Input
                        mt="1em"
                        placeholder="例：不定時的頭痛暈眩"
                        name="pain"
                        onChange={handleChange}
                        value={values.pain}
                      />
                      <ErrorMessage name="pain" />
                      <Text mt="1em" fontSize="1.5em" color="titleBlue">持續天數</Text>
                      <Dropdown options={options} name="duration" onChange={handleChange} value={values.duration} />
                      <ErrorMessage name="duration" />
                      <Box textAlign="center" mt="2em">
                        <Button mx="0.5em" type="submit">確認</Button>
                        <Button mx="0.5em" onClick={this.handleRemove}>{get(dots[openDot], 'pain') ? '刪除':'取消'}</Button>
                      </Box>
                    </Container>
                  </Form>
                )}
              </Formik>
            </Modal>
            <Box mt="2em" textAlign="center">
              <Button is={Link} px="3em" to="/memo/new/2" disabled={!dots.length}>下一步</Button>
            </Box>
          </Box>
        </Container>
      </Layout>
    );
  }
}

export default compose(withResponsive, creatingHoc)(Step1);
