import React, { useRef } from 'react';

import body from './body.svg';
import Box from '../../components/Box';
import Image from '../../components/Image';
import Circle from '../../components/Circle';

const BodyPoints = ({ onNewDot, onDotClick, dots, ...props }) => {
  const imageDom = useRef()
  const handleImageClick = e => {
    const location = imageDom.current.getBoundingClientRect()
    onNewDot([(e.clientX - location.x) / location.width * 100, (e.clientY - location.y) / location.height * 100])
  }
  const handleDotClick = (i) => () => onDotClick(i);
  return (
    <Box position="relative" {...props}>
      <Image
        ref={imageDom}
        onClick={onNewDot && handleImageClick}
        src={body}
      />
      {Boolean(dots.length) && dots.map((dot, i) => (
        <Box
          key={i}
          top={`${dot.position[1]}%`}
          left={`${dot.position[0]}%`}
          position="absolute"
          transform="translate(-50%, -50%)"
          onClick={onDotClick && handleDotClick(i)}
        >
          <Circle
            width="2rem"
            bg="orange"
            fontSize={i > 8 ? '1.5em' : '1.75em'}
            left={i > 8 && '48%'}
            color="white"
          >
            {i + 1}
          </Circle>
        </Box>
      ))}
    </Box>
  );
};

export default BodyPoints;
